@import url(https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: linear-gradient(180deg, #f1f6f9 0%, #ebf3f8 100%);
  color: white;
  font-family: "Space Grotesk", sans-serif;
}
.cuerpo {
  padding: 20px 20px 0 20px;
  height: 95vh;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  width: 90vw;
}

.cuerpo h2 {
  margin: 20px 0 20px 0;
  color: black;
}
.cuerpo .flex-lado {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}

.cuerpo .flex-lado .left-side {
  width: 50%;
  overflow: auto;
}
.cuerpo .flex-lado .left-side h2 {
  margin: 20px;
}
.cuerpo .flex-lado .left-side::-webkit-scrollbar {
  display: none;
}

.cuerpo .flex-lado .right-side {
  width: 50%;
}
.cuerpo .flex-lado .right-side h2 {
  margin: 20px;
}

.cuerpo .flex-lado .down-side h2 {
  margin: 20px;
}

.dash {
  display: flex;
  flex-direction: row;
}
.notas-container {
  display: flex;
  overflow-x: scroll;
  height: auto;
  display: grid;
  grid-auto-flow: column;
  margin-bottom: 20px;
}
.notas-container::-webkit-scrollbar {
  height: 15px;
}

.notas-container::-webkit-scrollbar-track {
  background: #e0e0e0;
}

/* Handle */
.notas-container::-webkit-scrollbar-thumb {
  background: #888;
}
.notas-container::-webkit-scrollbar-thumb:hover {
  background: rgb(20, 7, 77);
}
.notas-container .nota-card {
  color: #ffffff;
  padding: 20px;
  background-color: #14274e;
  border: 2px solid #cfcfcf;
  box-shadow: rgb(0 0 0 / 15%) 0px 5px 15px 0px;
  cursor: pointer;
  margin: 20px;
  min-height: 200px;
  width: 354px;
  border-radius: 15px;
}
.down-side {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: auto;
  flex-direction: column;
}

.search-wrapper {
  display: flex;
}
.search-wrapper .left-side {
  width: 50%;
}

@media (max-width: 992px) {
  .cuerpo {
    height: 86vh;
    width: auto;
    overflow: scroll;
  }

  .cuerpo .flex-lado {
    display: flex;
    justify-content: space-between;
    width: 100%;
    flex-direction: column;
  }
  .dash {
    display: flex;
    flex-direction: column;
  }
}

.nav {
  height: 95vh;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  padding-top: 20px;
  width: 120px;
  transition: width 0.3s;
  background: #14274e;
  overflow: hidden;
}

.nav:hover {
  background-color: #182f5e;
  cursor: pointer;
  width: 200px;
}

.nav a {
  color: white;
}

.nav .nav-log-container {
  width: 50px;
  margin-bottom: 20px;
  transition: 0.3s;
}

.nav:hover .nav-log-container {
  width: 100px;
  margin-bottom: 20px;
  padding: 10px;
  border-radius: 15px;
}

.nav:hover .logo-one-container span {
  display: block;
  text-align: center;
  font-size: 10px;
}

.logo-one-container span {
  display: none;
}

.button {
  background-color: #18a4e1;
}

.nav:hover .logo-one-container {
  flex-direction: row !important;
  align-items: center;
  justify-content: space-around;
  border: 1px solid white;
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 30px;
  padding: 20px;
}
.nav:hover .logo-one-container img {
  width: 20px;
}

@media (max-width: 992px) {
  .nav {
    height: 9vh;
    width: auto;
    flex-direction: row !important;
  }

  .nav:hover {
    background-color: #182f5e;
    cursor: pointer;
    width: 100vw;
    height: 13vh;
  }

  .nav .nav-log-container {
    width: 40px;
  }
  .nav:hover .nav-log-container {
    width: 50px;
    margin-bottom: 20px;
    padding: 10px;
    border-radius: 15px;
  }

  .nav:hover .flex-column-res {
    flex-direction: row !important;
  }
  .nav:hover .logo-one-container {
    flex-direction: row !important;
    align-items: center;
    justify-content: space-around;
    border: 1px solid white;
    display: flex;
    flex-direction: row;
    width: 180px;
    height: 40px;
  }
  .nav:hover .logo-one-container img {
    width: 20px;
  }
}

footer {
  background-color: #f1f6f9;
  height: 5vh;
  color: #053357;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.card {
  width: auto;
  min-height: 300px;
  color: #053357;
  padding: 20px;
  background-color: #fbf8f1;
  border: 2px solid #cfcfcf;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
  cursor: pointer;
  margin: 20px;
}

.card .bold {
  font-weight: bold;
}

.card:hover {
  border: 2px solid #18a4e1;
}

.nota-card img {
  width: 100px;
}
.nota-card h3 {
  font-size: 18px;
}

@media (max-width: 992px) {
  .card {
    width: auto;
  }
}

